body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Nunito', sans-serif !important;
}

.edit-link {
  padding: 26px;
  text-align: center;
  display: block;
  text-decoration: none;
  font-size: 24px;
}

.main-page .wrapper {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.text-view-page .wrapper {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg, #d53369 0%, #daae51 100%);
}

.text-view-page .textarea {
    outline: none;
    border: none;
    background: none;
    color: white;
    font-size: 40px;
    font-family: tahoma;
    padding: 26px;
    margin-bottom: 100px;
    text-align: center;
    overflow: hidden;
}

.text-view-page .textarea.edit {
    border: 1px solid rgba(255,255,255,.3);
}

.text-view-page .create {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 26px 0;
}

